'use client';

import { useSession } from 'next-auth/react';
import { useRouter, useSearchParams, usePathname } from 'next/navigation';
import { useEffect, useState, useMemo, useRef, Suspense } from 'react';
import { Card } from '@/components/ui/card';
import dynamic from 'next/dynamic';
import { useHybridCachedData } from '@/utils/useHybridCachedData';
import * as XLSX from 'xlsx';
import DownloadColumnsModal, { ColumnOption } from './components/downloadColumnsModal';
import { handleDownloadPDF } from './components/handleDownloadPDF';
import { permissions } from '@/lib/permissions';

// Section components
import ProjectsSection from './components/ProjectsSection';
import ReportsSection from './components/ReportsSection';
import EventsSection from './components/EventsSection';
import InstitutionsSection from './components/InstitutionsSection';
import BeneficiariesSection from './components/BeneficiariesSection';
import BeneficiaryMessagesSection from './components/BeneficiaryMessagesSection';
import BeneficiaryRequestsSection from './components/BeneficiaryRequestsSection';
import BeneficiaryRequestResponseSection from './components/BeneficiaryRequestResponseSection';
import ChartsSection from './components/ChartsSection';
import AdminSection from './components/AdminSection';
import HomePageSection from './components/HomePageSection';

// ...Dynamic imports for forms and column/data-table imports as before

const sections = [
  'events',
  'projects',
  'reports',
  'institutions',
  'beneficiaries',
  'beneficiaryMessages',
  'beneficiaryRequests',
  'beneficiaryRequestResponse',
  'charts',
  'Home Page',
  'admin',
] as const;
type Section = (typeof sections)[number];

const rowsPerPageOptions = [5, 10, 25, 50];

const permKeyMap: Record<Section, keyof typeof permissions> = {
  projects: 'Project',
  reports: 'Report',
  admin: 'User',
  events: 'Event',
  institutions: 'Institution',
  beneficiaries: 'Beneficiary',
  beneficiaryMessages: 'BeneficiaryMessage',
  beneficiaryRequests: 'BeneficiaryRequest',
  beneficiaryRequestResponse: 'BeneficiaryRequestResponse',
  charts: 'Project',
  'Home Page': 'HomePage',
};

function canView(type: keyof typeof permissions, role: string) {
  return permissions[type]?.view?.includes(role);
}

export default function AdminDashboardPage() {
  // --- State and hooks as before ---
  const { data: session, status } = useSession();
  const router = useRouter();
  const [activeSection, setActiveSection] = useState<Section | null>(null);
  const [data, setData] = useState<any[]>([]);
  const [editRecord, setEditRecord] = useState<any | null>(null);
  const [showCreateForm, setShowCreateForm] = useState<boolean>(false);
  const [showExecMsgForm, setShowExecMsgForm] = useState<boolean>(false);
  const [sortBy, setSortBy] = useState('createdAt');
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('desc');
  const [rowsPerPage, setRowsPerPage] = useState<number>(10);
  const [page, setPage] = useState<number>(1);
  const [search, setSearch] = useState('');
  const sectionRef = useRef(activeSection);

  const [showCreateBeneficiaryMessageForm, setShowCreateBeneficiaryMessageForm] = useState(false);
  const [showCreateBeneficiaryRequestForm, setShowCreateBeneficiaryRequestForm] = useState(false);
  const [selectedBeneficiaryId, setSelectedBeneficiaryId] = useState<string | null>(null);

  const [showRespondForm, setShowRespondForm] = useState(false);
  const [respondRequestId, setRespondRequestId] = useState<string | null>(null);

  const searchParams = useSearchParams();
  const pathname = usePathname();

  const messagesRef = useRef<HTMLDivElement>(null);
  const homepageRef = useRef<HTMLDivElement>(null);
  const defaultRef = useRef<HTMLDivElement>(null);

  const [downloadModalOpen, setDownloadModalOpen] = useState(false);
  const [downloadColumns, setDownloadColumns] = useState<ColumnOption[]>([]);
  const [pendingDownloadData, setPendingDownloadData] = useState<any[]>([]);

  const [responsesCountMap, setResponsesCountMap] = useState<Record<string, number>>({});
  const [messagesCountMap, setMessagesCountMap] = useState<Record<string, number>>({});
  const [requestsCountMap, setRequestsCountMap] = useState<Record<string, number>>({});

  const {
    data: homepageData,
    isLoading: loadingHomepage,
    refresh: refreshHomepage,
  } = useHybridCachedData('admin-dashboard-homepage-v1', async () => {
    const res = await fetch('/api/homepage-content');
    if (!res.ok) throw new Error('Failed to fetch homepage data');
    const d = await res.json();
    return d ? [d] : [];
  });

  const {
    data: messagesData,
    isLoading: loadingMessages,
    refresh: refreshMessages,
  } = useHybridCachedData('admin-dashboard-messages-v1', async () => {
    const res = await fetch('/api/executive-messages');
    if (!res.ok) throw new Error('Failed to fetch messages data');
    return await res.json();
  });

  // --- Effects for permissions, data, search, etc. (same as before) ---

  // ...All useEffects from your original code

  // --- Helpers ---
  function norm(v?: string) {
    return (v ?? '').trim().toLowerCase();
  }

  function filterOwn(section: string, rows: any[]) {
    // ...same as your code
  }

  async function handleCardClick(section: Section) {
    // ...same as your code
  }

  function handleEdit(record: any) {
    // ...same as your code
  }

  async function handleDelete(
    type:
      | 'projects'
      | 'reports'
      | 'users'
      | 'events'
      | 'institutions'
      | 'beneficiaries'
      | 'beneficiaryMessages'
      | 'beneficiaryRequests'
      | 'beneficiaryRequestResponse'
      | 'homepage-content'
      | 'executive-messages',
    id: string | number
  ) {
    // ...same as your code
  }

  // --- Section features (used for prop passing) ---
  const sectionFeatures = {
    // ...same as your sectionFeatures object
  };

  const role = session?.user?.role ?? 'guest';
  const activePermKey = activeSection ? permKeyMap[activeSection] : undefined;
  const canViewThisSection = activePermKey ? canView(activePermKey, role) : true;

  const sortedData = useMemo(() => {
    // ...same as your code
  }, [data, sortBy, sortOrder, activeSection]);

  const filteredData = useMemo(() => {
    // ...same as your code
  }, [sortedData, search, activeSection]);

  const pageCount = Math.ceil(filteredData.length / rowsPerPage);
  const paginatedData = useMemo(
    () => filteredData.slice((page - 1) * rowsPerPage, page * rowsPerPage),
    [filteredData, page, rowsPerPage]
  );

  // --- TableActions as before ---
  const TableActions = ({
    data,
    columns,
    tableRef,
  }: {
    data: any[];
    columns: any[];
    tableRef: React.RefObject<HTMLDivElement>;
  }) => {
    // ...same as your code
  };

  // --- Render active section ---
  function renderSection() {
    switch (activeSection) {
      case 'projects':
        return (
          <ProjectsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('projects', id)}
            TableActions={TableActions}
          />
        );
      case 'reports':
        return (
          <ReportsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('reports', id)}
            TableActions={TableActions}
          />
        );
      case 'events':
        return (
          <EventsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('events', id)}
            TableActions={TableActions}
          />
        );
      case 'institutions':
        return (
          <InstitutionsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('institutions', id)}
            TableActions={TableActions}
          />
        );
      case 'beneficiaries':
        return (
          <BeneficiariesSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('beneficiaries', id)}
            messagesCountMap={messagesCountMap}
            requestsCountMap={requestsCountMap}
            currentUserRole={role}
            TableActions={TableActions}
            onAddMessage={(id: string) => {
              setSelectedBeneficiaryId(id);
              setShowCreateBeneficiaryMessageForm(true);
            }}
            onAddRequest={(id: string) => {
              setSelectedBeneficiaryId(id);
              setShowCreateBeneficiaryRequestForm(true);
            }}
          />
        );
      case 'beneficiaryMessages':
        return (
          <BeneficiaryMessagesSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('beneficiaryMessages', id)}
            currentUserRole={role}
            TableActions={TableActions}
          />
        );
      case 'beneficiaryRequests':
        return (
          <BeneficiaryRequestsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('beneficiaryRequests', id)}
            responsesCountMap={responsesCountMap}
            TableActions={TableActions}
            onRespond={(id: string) => {
              setRespondRequestId(id);
              setShowRespondForm(true);
            }}
          />
        );
      case 'beneficiaryRequestResponse':
        return (
          <BeneficiaryRequestResponseSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('beneficiaryRequestResponse', id)}
            TableActions={TableActions}
          />
        );
      case 'charts':
        return <ChartsSection />;
      case 'admin':
        return (
          <AdminSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('users', id)}
            TableActions={TableActions}
          />
        );
      case 'Home Page':
        return (
          <HomePageSection
            homepageData={homepageData}
            messagesData={messagesData}
            handleEdit={handleEdit}
            handleDelete={handleDelete}
            showExecMsgAddButton={
              activeSection === 'Home Page' && !showCreateForm && !showExecMsgForm && !editRecord
            }
            onAddExecMsg={() => {
              setEditRecord(null);
              setShowExecMsgForm(true);
              setShowCreateForm(false);
            }}
            TableActions={TableActions}
            homepageRef={homepageRef}
            messagesRef={messagesRef}
          />
        );
      default:
        return null;
    }
  }

  // --- Main render ---
  if (status === 'loading') {
    return <div>Checking session ...</div>;
  }

  return (
    <div className="p-6">
      <div className="flex flex-wrap gap-4 mb-4">
        {sections.map((section) => (
          <Card
            key={section}
            onClick={() => handleCardClick(section)}
            className={`
              cursor-pointer 
              px-4 py-3
              flex items-center justify-center text-center
              rounded-sm  
              whitespace-nowrap
              font-medium
              transition 
              ${
                activeSection === section
                  ? 'bg-[#9f004d] text-white shadow-2xl border-2 border-[#9f004d] scale-105'
                  : 'text-gray-400 opacity-70 grayscale hover:opacity-100 hover:grayscale-0'
              }
              `}
            style={{ width: 'auto', minWidth: '3rem', maxWidth: '100%' }}
          >
            <span className="px-2">{section.replace(/([A-Z])/g, ' $1').toUpperCase()}</span>
          </Card>
        ))}
      </div>
      {activeSection && <div className="mt-6">{renderSection()}</div>}
      {/* ...pagination and modals as before */}
      <DownloadColumnsModal
        isOpen={downloadModalOpen}
        onClose={() => setDownloadModalOpen(false)}
        columns={downloadColumns}
        onDownload={(selectedColumns) => {
          handleDownloadPDF(
            pendingDownloadData,
            selectedColumns,
            activeSection ? activeSection.charAt(0).toUpperCase() + activeSection.slice(1) : 'Data'
          );
        }}
      />
    </div>
  );
}
